import "./styles.scss";
import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import {
  useGetProductsDetailQuery,
  useGetPublishedProductsQuery,
} from "../../store/components/products/productsApi";
import { PAGE_SIZE } from "../../utils/constants";
import { useSelector } from "react-redux";
import { getUserInfo } from "../../store/selector/RootSelector";
import { useDispatch } from "react-redux";
import ProductsRender from "./ProductsRender";

const ShopDetailSection = ({
  pagenumber,
  keyword,
  brand,
  shopId,
  productShop,
}: any) => {
  const navigate = useNavigate();
  const dispatch = useDispatch();

  const [dataFetched, setdataFetched] = useState<any>([]);

  const [currentPage, setCurrentPage] = useState<any>(1);
  const [total, setTotal] = useState<any>(1);

  const [params, setParams] = useState<any>({
    page: pagenumber ?? 1,
    keyword: keyword ?? "",
    brand: brand ?? "",
    limit: PAGE_SIZE,
    order: "desc",
    orderBy: "createdAt",
  });

  useEffect(() => {
    setParams({
      ...params,
      page: pagenumber ?? 1,
      keyword: keyword ?? "",
      brand: brand ?? "",
    });
  }, [pagenumber, keyword, brand]);

  const {
    data: dataProducts,
    error,
    isSuccess,
    isLoading,
  } = useGetPublishedProductsQuery(
    {
      product_shop: shopId,
    },
    {
      refetchOnMountOrArgChange: true,
      skip: false,
    }
  );
  useEffect(() => {
    if (isSuccess) {
      setdataFetched(dataProducts?.metadata?.products);
      setTotal(dataProducts?.totalPages);
      setCurrentPage(dataProducts?.page);

      const urls: any = [];

      const productsArr = dataProducts?.metadata?.products ?? [];
      if (Array.isArray(productsArr) && productsArr.length > 0) {
        productsArr.map((product) => {
          urls.push("http://localhost:5000/api/products/detail/" + product._id);
        });
        // const zz = concurrencyRequest(urls, 3);
      }
    }
  }, [dataProducts]);
  const userInfo = useSelector(getUserInfo);

  const concurrencyRequest = async (urls: any, maxNum: any) => {
    if (urls.length === 0) {
      // Nu danh sch URLs trng, resolve ngay lp tc vi mng kt qu trng
      return Promise.resolve([]);
    }

    const results: any = []; // Mng kt qu t cc yu cu
    let index = 0; // Ch s ca URL ang c x l
    let count = 0; // S lng yu cu  hon thnh

    async function request() {
      if (index === urls.length) return; // Nu  x l tt c cc URL, thot khi hm

      const i = index; // Lu ch s  s dng trong async function
      const url = urls[index++]; // Ly URL v tng ch s

      try {
        // Thc hin yu cu fetch v lu kt qu vo mng
        results[i] = await fetch(url);
      } catch (err) {
        // Nu c li, lu li vo mng kt qu
        results[i] = err;
      } finally {
        // Tng bin m v kim tra hon thnh tt c cc yu cu
        if (++count === urls.length) {
          console.log("Hon thnh tt c yu cu");
          return Promise.resolve(results);
        }

        // t thi gian ch 1 giy v sau  gi li hm yu cu
        setTimeout(request, 1000);
      }
    }

    const maxConcurrency = Math.min(maxNum, urls.length); // S ln yu cu ti a c th c thc hin ng thi
    // Bt u thc hin yu cu ng thi
    const zzz = Array.from({ length: maxConcurrency }, () => setTimeout(request, 1000));
    // console.log(`:::zzz::`, zzz);
  };
  return (
    <ProductsRender
      isLoading={isLoading}
      keyword={keyword}
      brand={brand}
      error={error}
      productShop={productShop}
      dataFetched={dataFetched}
      userInfo={userInfo}
      total={total}
      currentPage={currentPage}
    ></ProductsRender>
  );
};

export default ShopDetailSection;
